.container.contact_container
{
    width:78%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap:1.2rem;
}
.contact_options{
    display: flex;
    flex-direction: column;
    gap: 1.2em;
}
.contact_option
{
    background: rgba(166, 255, 0, 0.261);
    padding: 1.2rem;
    border-radius:1.2rem;
    text-align: center;
    border:1px solid transparent;
    transition:350ms;
}
.contact_options:hover{
    background:transparent;
    border-color:red ;
}
.contact_option-icon
{
    font-size:1.5rem ;
    margin-bottom:0.5rem;
}
.contact_option a{
    margin-top:0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

form{
    display:flex;
    flex-direction: column;
    gap:1.2rem;
}
input,textarea{
    width:100%;
    padding:1.5rem;
    border-radius:0.5rem;
    background: transparent;
    border:2px solid rgba(255, 255, 255, 0.362);
    resize:none;
    color:white;

}



@media screen and (
    max-width: 1024px
)
{
   .container.contact_container
   {
       grid-template-columns: 1fr;
       gap:2rem;
   }
}
/*small devices*/
@media screen and (
    max-width: 600px
)
{
.container.contact_container
{
    width:var(--container-width-sm)
}
}