.experience_container
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap:2rem;
}
.experience_container > div{
    background: rgba(255, 0, 0, 0.114);
    padding:2.4rem 5rem;
    border-radius: 2rem;
    border:1px solid transparent;
    transition:300ms;
}
.experience_container>div:hover{
    background: transparent;
    border-color: aliceblue;
    cursor:grabbing;
}
.experience_container >div h3{
text-align: center;
margin-bottom: 2rem;
color:var(--color-primary)
}
.experience_content
{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}
.experience_details
{
    display: flex;
    gap:1rem;
}
.experience_details-icon
{
    margin-top: 6px;
    color:var(--color-primary)
}




@media screen and (
    max-width: 1024px
)
{
   .experience_container
   {
       grid-template-columns: 1fr;
   }
   .experience_container > div {
       width: 80%;
       padding:2rem;
margin:0 auto;
   }
   .experience_content
   {
       padding: 1rem;
   }
}
/*small devices*/
@media screen and (
    max-width: 600px
)
{
  
.experience_container
{
    gap:1rem;
}
   .experience_container > div
   {
       width:100%;
       padding:2rem 1rem;
   }
}